import { Component, h, Prop, Host, FunctionalComponent, Listen, State, Method } from '@stencil/core';
import ChevronDown from '@material-design-icons/svg/outlined/expand_more.svg';

@Component({
  tag: 'rwc-select',
  styleUrl: 'select.scss',
  shadow: true
})
export class RwcSelect {
  /**
   * The reference to the slot containing options.
   */
  private optionsSlot: HTMLSlotElement;

  /**
   * The reference to the dropdown HTML element.
   */
  private dropdownElement: HTMLRwcDropdownElement;

  /**
   * Path to the icon.
   */
  @Prop({ reflect: true }) icon: string = '';

  /**
   * Change cursor to pointer on hover.
   */
  @Prop() pointer: boolean = false;

  /**
   * Value of selected field.
   */
  @Prop({ mutable: true }) value = '';

  /**
   * Text of selected field.
   */
  @Prop({ mutable: true }) text = '';

  /**
   * State of icon.
   */
  @State() isOpen: boolean = false;

  /**
   * Handles the click event inside the select component including children (option elements inside dropdown).
   */
  @Listen('click')
  onSelectClick(event: MouseEvent): void {
    this.optionsSlot.assignedElements().forEach((element: HTMLOptionElement) => {
      if (event.target === element) {
        this.setValue(element.value);
      }
    });
  }

  /**
   * Handles the open event of the dropdown component.
   */
  @Listen('dropdownOpen')
  onDropdownOpen(): void {
    this.isOpen = true;
  }

  /**
   * Handles the close event of the dropdown component.
   */
  @Listen('dropdownClose')
  onDropdownClose(): void {
    this.isOpen = false;
  }

  /**
   * Initializes the default active element and sets component value to the value of the active element.
   */
  componentDidLoad(): void {
    this.setValue(this.value);
  }

  /**
   * Sets the value and text of the component to the value of the option element containing the passed value.
   */
  @Method()
  async setValue(value: string): Promise<void> {
    this.optionsSlot.assignedElements().forEach((element: HTMLOptionElement) => {
      if (value === element.value) {
        this.value = element.value;
        this.text = element.text;
        this.dropdownElement.setState(false);
      }
    });
  }

  /**
   * Renders the component.
   */
  render(): FunctionalComponent {
    return (
      <Host>
        <div class={ {
          container: true,
          pointer: this.pointer,
        } }
        >
          <div class="wrapper">
            <div class="value">
              { this.text }
            </div>
            <div 
              slot="right" 
              class={ {
                rotate: true,
                open: this.isOpen
              } }>
              <slot name="icon-right">
                { this.renderIcon() }
              </slot>
            </div>
          </div>
          <rwc-dropdown
            class="dropdown"
            open-mode="toggle"
            size="parent"
            mode='fixed'
            ref={ (dropdown: HTMLRwcDropdownElement) => this.dropdownElement = dropdown }
          >
            <slot
              name="option"
              ref={ (slot: HTMLSlotElement) => this.optionsSlot = slot } />
          </rwc-dropdown>
        </div>
      </Host>
    );
  }

  /**
   * Renders the icon element.
   */
  renderIcon(): FunctionalComponent {
    if (this.icon) {
      return (
        <img src={ this.icon } alt="icon"></img>
      )
    }
    return (
      <div innerHTML={ ChevronDown } class="icon svg-icon"></div>
    )
  }
}
