@import 'global.scss';

:host {
  /**
  * @prop --select-animation-duration: The duration of animation of the select.
  *
  * @prop --select-border: The border of the select.
  * @prop --select-border-radius: The border radius of the select.
  *
  * @prop --select-option-hover-background-color: The color of option on hover of the select.
  *
  * @prop --select-font-color: The color of the text of the select.
  *
  * @prop --select-icon-color: The color of the icon of the select.
  * @prop --select-icon-size: The size of the icon of the select.
  * @prop --select-icon-transition: The icon animation of the select.
  *
  * @prop --select-padding: The padding of the select.
  * @prop --select-font-size: The size of the font of the select.
  *
  * @prop --select-dropdown-background-color: The background color of the dropdown.
  * @prop --select-dropdown-border: The border of the dropdown.
  * @prop --select-dropdown-border-radius: The border radius of the dropdown.
  * @prop --select-dropdown-box-shadow: The box shadow of the dropdown.
  * @prop --select-dropdown-max-width: The max width of the dropdown.
  * @prop --select-dropdown-container-padding: The container padding of the dropdown.
  * @prop --select-dropdown-container-margin: The container margin of the dropdown.
  *
  * @prop display: The display mode of the host element.
  */

  --select-animation-duration: 100ms;

  --select-border: 1px solid #D2D3D3;
  --select-border-radius: #{$rwc-border-radius--md};

  --select-option-hover-background-color: #{$rwc-color-grey-200};

  --select-font-color: #{$rwc-color-grey-900};
  --select-font-size: #{$rwc-font-size-body-md};

  --select-icon-color: #{$rwc-color-grey-900};
  --select-icon-size: #{$rwc-font-size-body-lg};
  --select-icon-transition: transform var(--select-animation-duration) linear;

  --select-padding: #{$rwc-spacing--xs} #{$rwc-spacing--sm};

  //Dropdown styles
  --select-dropdown-background-color:  #{$rwc-color-foundation-white};
  --select-dropdown-border: 1px solid #{$rwc-color-border-primary};
  --select-dropdown-border-radius: 0.5rem;
  --select-dropdown-box-shadow: #{$rwc-box-shadow-sm};
  --select-dropdown-max-width:  100%;
  --select-dropdown-container-padding: #{$rwc-spacing--xs} #{$rwc-spacing--md};
  --select-dropdown-container-margin: #{$rwc-spacing--xs} 0 0 0;

  display: block;
}

.dropdown {
  --dropdown-background-color: var(--select-dropdown-background-color);
  --dropdown-border: var(--select-dropdown-border);
  --dropdown-border-radius: var( --select-dropdown-border-radius);
  --dropdown-box-shadow: var(--select-dropdown-box-shadow);
  --dropdown-max-width: var( --select-dropdown-max-width);
  --dropdown-container-padding: var(--select-dropdown-container-padding);
  --dropdown-container-margin: var(--select-dropdown-container-margin);
}

.container {
  border: var(--select-border);
  align-items: center;
  font-size: var(--select-font-size);
  padding: var(--select-padding);
  color: var(--select-font-color);
  border-radius: var(--select-border-radius);
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.icon {
  align-self: center;
  flex-shrink: 0;
  height: var(--select-icon-size);
  width: var(--select-icon-size);

  svg {
    height: 100%;
    width: 100%;
  }

  path {
    fill: var(--select-icon-color);
  }
}

img {
  height: var(--select-icon-size);
  width: var(--select-icon-size);
}

.rotate {
  transition: var(--select-icon-transition);
}

.rotate.open{
  transform: rotate(180deg);
}

.dropdown ::slotted(option:hover) {
  background-color: var(--select-option-hover-background-color);
}

.dropdown ::slotted(option) {
  text-overflow: ellipsis;
  overflow: hidden;
}

.pointer {
  cursor: pointer;
}
